.pagin {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 10px;
  color: #285473;
  cursor: pointer;
  font-size: 19px;
  bottom: 0;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  width: 150px;
  text-align: center;
  position: relative;
}

.active {
  color: black;
  border-bottom: 2px solid black;
}

.button {
  border: none;
  font-size: 20px;
  color: #285473;
  cursor: pointer;
  background: none;
}

.button:hover {
  font-weight: bold;
  color: black;
}

.button:first-child {
  position: absolute;
  left: -15px;
}

.button:last-child {
  position: absolute;
  right: -15px;
}